import { Component, OnInit } from '@angular/core';
import { BehaviorSubject } from 'rxjs';
//Toastr
import { ToastrService } from 'ngx-toastr';

//Service
import { SettingsService } from '../settings.service'

@Component({
  selector: 'kt-logging',
  templateUrl: './logging.component.html',
  styleUrls: ['./logging.component.scss']
})
export class LoggingComponent implements OnInit {

  periods = {
    api: "3 years",
    authentication: "3 years",
    authorization: "3 years",
    accounting: "3 years"
  }
  api_log_period = "3 years"
  authe_log_period = "3 years"
  autho_log_period = "3 years"
  acc_log_period = "3 years"

  validation = new BehaviorSubject({})
  loading = new BehaviorSubject(false)

  constructor(
    private service: SettingsService,
    private toastr: ToastrService
  ) { }

  ngOnInit() {
    this.init()
  }

  makeClone(src){
    return JSON.parse(JSON.stringify(src));
  }

  init(){
    // this.service.smtp_get().subscribe( data => {
    //   console.log(data)
    //   this.data = data.smtp
    //   this.data_old = this.makeClone(data.smtp)
    //   this.loading.next(false)
    // })
  }

  del(type){
    this.loading.next(true)
    let data = {
      period: this.periods[type],
      target: type
    }
    if ( type == 'api' ) delete data.target;
    this.service.del_reports(data, type).subscribe( data => {
      console.log(data)
      this.loading.next(false)
      if (data.error && data.error.status){
        this.toastr.error('Unexpected Error!');
        return;
      }
      if (data.result == 0){
        this.toastr.warning('Nothing Deleted!');
        return;
      } else {
        this.toastr.warning('Deleted '+data.result+'entries');
      }

    })
  }

}
