import { Component, OnInit } from '@angular/core';
import { BehaviorSubject } from 'rxjs';
//Toastr
import { ToastrService } from 'ngx-toastr';

//Service
import { SettingsService } from '../settings.service'

@Component({
  selector: 'kt-passpolicy',
  templateUrl: './passpolicy.component.html',
  styleUrls: ['./passpolicy.component.scss']
})
export class PasspolicyComponent implements OnInit {

  data = {
    api_pw_length: 6,
    api_pw_lowercase: 0,
    api_pw_numbers: 0,
    api_pw_same: 0,
    api_pw_special: 0,
    api_pw_uppercase: 0,
    tac_pw_length: 6,
    tac_pw_lowercase: 0,
    tac_pw_numbers: 0,
    tac_pw_same: 0,
    tac_pw_special: 0,
    tac_pw_uppercase: 0
  }
  data_old = {}

  validation = new BehaviorSubject({})
  loading = new BehaviorSubject(true)

  constructor(
    private service: SettingsService,
    private toastr: ToastrService
  ) { }

  ngOnInit() {
    this.init()
  }

  makeClone(src){
    return JSON.parse(JSON.stringify(src));
  }

  init(){
    this.service.passpolicy_get().subscribe( data => {
      console.log(data)
      this.data = data.policy
      this.data_old = this.makeClone(data.policy)
      this.loading.next(false)
    })
  }

  save(){
    console.log(this.data)
    this.loading.next(false)
    if (JSON.stringify(this.data) == JSON.stringify(this.data_old)){
      this.toastr.warning('No Changes Detected!')
      this.loading.next(false)
      return;
    }

    let data = this.makeClone(this.data)

    console.log(data)

    data.api_pw_lowercase+= 0;
    data.api_pw_numbers+= 0;
    data.api_pw_same+= 0;
    data.api_pw_special+= 0;
    data.api_pw_uppercase+= 0;
    data.tac_pw_lowercase+= 0;
    data.tac_pw_numbers+= 0;
    data.tac_pw_same+= 0;
    data.tac_pw_special+= 0;
    data.tac_pw_uppercase+= 0;

    this.service.passpolicy_post(data).subscribe( data => {
      console.log(data)
      this.validation.next(data.error.validation)
      if (data.error.status){
        this.loading.next(false)
        return
      }
      this.toastr.success('Changes Saved!')
      this.init()
      this.loading.next(false)
    })
  }

}
