import { Component, OnInit } from '@angular/core';
import { BehaviorSubject } from 'rxjs';
//Toastr
import { ToastrService } from 'ngx-toastr';

//Service
import { SettingsService } from '../settings.service'

@Component({
  selector: 'kt-time',
  templateUrl: './time.component.html',
  styleUrls: ['./time.component.scss']
})
export class TimeComponent implements OnInit {

  list = {
    timezones: {
      multiple: false,
      title: 'Timezone',
      title_sidebar: 'Timezones List',
      search: true,
      apiurl: 'api/settings/time/timezones/',
    }
  }
  data = {
    timezone: [],
    ntp_list: ''
  }
  data_old = {
    timezone: [],
    ntp_list: ''
  }

  validation = new BehaviorSubject({})
  loading = new BehaviorSubject(true)
  loading_check = new BehaviorSubject(false)
  check_status = ''


  constructor(
    private service: SettingsService,
    private toastr: ToastrService
  ) { }

  ngOnInit() {
    this.init()
  }

  init(){
    this.service.time_get().subscribe( data => {
      // console.log(data)
      this.data = data.time
      this.data_old = this.makeClone(data.time)
      this.loading.next(false)
    })
  }

  setTimezone(data){
    this.data.timezone = data
  }

  makeClone(src){
    return JSON.parse(JSON.stringify(src));
  }

  save(){
    console.log(this.data)
    this.loading.next(false)
    if (JSON.stringify(this.data) == JSON.stringify(this.data_old)){
      this.toastr.warning('No Changes Detected!')
      this.loading.next(false)
      return;
    }

    let data = this.makeClone(this.data)
    data.timezone = (data.timezone[0]) ? data.timezone[0].id : 0;

    console.log(data)

    this.service.time_post(data).subscribe( data => {
      console.log(data)
      this.validation.next(data.error.validation)
      if (data.error.status){
        this.loading.next(false)
        return
      }
      this.toastr.success('Changes Saved!')
      this.init()
      this.loading.next(false)
    })
  }

  check(){
    this.loading_check.next(true)
    this.service.time_status().subscribe( data => {
      this.check_status = data.output
      this.loading_check.next(false)
    })
  }

}
