import { Component, OnInit } from '@angular/core';
import { BehaviorSubject } from 'rxjs';
//Toastr
import { ToastrService } from 'ngx-toastr';

//Service
import { SettingsService } from '../settings.service'

@Component({
  selector: 'kt-network',
  templateUrl: './network.component.html',
  styleUrls: ['./network.component.scss']
})
export class NetworkComponent implements OnInit {

  inter_list = []
  inter = ''
  data = {
    network_address: "",
    network_dns1: "",
    network_dns2: "",
    network_gateway: "",
    network_mask: "",
  }
  data_old = {}

  validation = new BehaviorSubject({})
  loading = new BehaviorSubject(true)

  constructor(
    private service: SettingsService,
    private toastr: ToastrService
  ) { }

  ngOnInit() {
    this.init()
  }

  makeClone(src){
    return JSON.parse(JSON.stringify(src));
  }

  init(){
    this.service.network_get(this.inter).subscribe( data => {
      console.log(data)
      this.inter = data.list[0];
      this.inter_list = data.list;
      this.data = data.interface
      this.data_old = this.makeClone(data.interface)
      this.loading.next(false)
    })
  }

  save(){
    console.log(this.data)
    this.loading.next(true)
    if (JSON.stringify(this.data) == JSON.stringify(this.data_old)){
      this.toastr.warning('No Changes Detected!')
      this.loading.next(false)
      return;
    }

    let data = this.makeClone(this.data)
    data.network_interface = this.inter
    let ipv6 = data.network_address6.split('/')
    data.network_address6 = ipv6[0] || ''
    data.network_prefix6 = ipv6[1] || 64

    console.log(data)

    this.service.network_post(data).subscribe( data => {
      console.log(data)
      this.validation.next(data.error.validation)
      if (data.error.status){
        this.loading.next(false)
        return
      }
      this.toastr.success('Changes Saved!')
      this.init()
      this.loading.next(false)
    })
  }

}
