import { Injectable } from '@angular/core';
import { HttpClient, HttpParams } from '@angular/common/http';
import { map, first } from 'rxjs/operators';

const API_URL = 'api/settings/';

@Injectable({
  providedIn: 'root'
})
export class SettingsService {

  constructor(private http: HttpClient) {}

  time_get() {
    return this.http.get<any>(API_URL + 'time/',)
      .pipe(
        first(),
        map( (data) => {
          return data
        } )
      )
  }
  time_status() {
    return this.http.get<any>(API_URL + 'time/status/',)
      .pipe(
        first(),
        map( (data) => {
          return data
        } )
      )
  }
  time_post(settings:{}) {
    return this.http.post<any>(API_URL + 'time/', settings)
      .pipe(
        first(),
        map( (data) => {
          return data
        } )
      )
  }

  passpolicy_get() {
    return this.http.get<any>(API_URL + 'pwpolicy/',)
      .pipe(
        first(),
        map( (data) => {
          return data
        } )
      )
  }

  passpolicy_post(settings:{}) {
    return this.http.post<any>(API_URL + 'pwpolicy/', settings)
      .pipe(
        first(),
        map( (data) => {
          return data
        } )
      )
  }

  network_get(inter:string) {
    const params = new HttpParams()
    .set('interface', inter);
    return this.http.get<any>(API_URL + 'network/interface/', {params})
      .pipe(
        first(),
        map( (data) => {
          return data
        } )
      )
  }

  network_list(ip:number = 1) {
    const params = new HttpParams()
    .set('ip', ip.toString());
    return this.http.get<any>(API_URL + 'network/interface/list/', {params})
      .pipe(
        first(),
        map( (data) => {
          return data
        } )
      )
  }

  network_post(settings:{}) {
    return this.http.post<any>(API_URL + 'network/interface/', settings)
      .pipe(
        first(),
        map( (data) => {
          return data
        } )
      )
  }

  smtp_get() {
    return this.http.get<any>(API_URL + 'smtp/')
      .pipe(
        first(),
        map( (data) => {
          return data
        } )
      )
  }

  smtp_test(smtp_test_email:string) {
    return this.http.post<any>(API_URL + 'smtp/test/', { smtp_test_email } )
      .pipe(
        first(),
        map( (data) => {
          return data
        } )
      )
  }

  smtp_post(settings:{}) {
    return this.http.post<any>(API_URL + 'smtp/', settings)
      .pipe(
        first(),
        map( (data) => {
          return data
        } )
      )
  }

  del_reports(settings:{}, urlType:string = 'api') {
    let url = (urlType == 'api') ? 'api/logging/delete/' : 'api/tacacs/reports/delete/'
    return this.http.post<any>(url, settings)
      .pipe(
        first(),
        map( (data) => {
          return data
        } )
      )
  }

  ha_get(){
    return this.http.get<any>(API_URL + 'ha/')
      .pipe(
        first(),
        map( (data) => {
          return data
        } )
      )
  }

  ha_post(settings){
    return this.http.post<any>(API_URL + 'ha/', settings)
      .pipe(
        first(),
        map( (data) => {
          return data
        } )
      )
  }

  ha_status(){
    return this.http.post<any>(API_URL + 'ha/status/', {})
      .pipe(
        first(),
        map( (data) => {
          return data
        } )
      )
  }

  ha_slave_list(slaves_ip){
    return this.http.post<any>(API_URL + 'ha/slave/list/', {slaves_ip})
      .pipe(
        first(),
        map( (data) => {
          return data
        } )
      )
  }

  notification_get(){
    return this.http.get<any>('api/notification/settings/')
      .pipe(
        first(),
        map( (data) => {
          return data
        } )
      )
  }

  notification_post(settings){
    return this.http.post<any>('api/notification/settings/', settings)
      .pipe(
        first(),
        map( (data) => {
          return data
        } )
      )
  }
}
