import { Component, OnInit } from '@angular/core';

//RXJS
import { BehaviorSubject, forkJoin } from 'rxjs';
import { map } from 'rxjs/operators';
//Service
import { SettingsService } from '../settings.service'
//Toastr
import { ToastrService } from 'ngx-toastr';

@Component({
  selector: 'kt-ha',
  templateUrl: './ha.component.html',
  styleUrls: ['./ha.component.scss']
})
export class HaComponent implements OnInit {

  validation = new BehaviorSubject({})
  loading = new BehaviorSubject(true)

  server = {
    config_uid: "",
    interface: "",
    ipaddr: "",
    ipaddr_master: "",
    ipaddr_slave: "",
    psk_master: this.keyGen(),
    psk_slave: "",
    role: "disabled",
    slave_id_list: "",
    slaves_ip: ""
  }

  masterInfo = new BehaviorSubject({})
  slaveInfo = new BehaviorSubject([])

  inter = ''
  inter_list = []
  output = new BehaviorSubject('')
  rootpw = new BehaviorSubject(false)

  constructor(
    private service: SettingsService,
    private toastr: ToastrService
  ) { }

  ngOnInit() {
    this.initInfo()
  }

  initInfo(){
    this.slaveInfo.next([])
    this.masterInfo.next({})
    forkJoin(this.service.network_list(), this.service.ha_get(),)
    .pipe( map( ([inter,data]) => { return [inter,data]}))
    .subscribe( data => {
      console.log(data)
      this.inter_list = data[0].list

      this.inter = this.inter_list[0]
      if (data[1].result && data[1].result.server)
        this.server = data[1].result.server

      if (data[1].result && data[1].result.server_list){
        if (data[1].result.server_list.master && data[1].result.server_list.master.ipaddr)
          this.masterInfo.next(data[1].result.server_list.master)
        if (data[1].result.server_list.slave){
          let slaves = []
          for (let key in data[1].result.server_list.slave) {
            data[1].result.server_list.slave[key].slave_id = key
            slaves[length] = data[1].result.server_list.slave[key]
          }
          //console.log(slaves)
          this.slaveInfo.next(slaves)
        }

        //console.log(this.masterInfo.getValue())
      }

      this.loading.next(false)

    })
  }

  keyGen(){
    let length = 32//Math.floor(Math.random() * (+16 - +16)) + +16; //224
    var text = "";
    var possible = "ABCDEFGHIJKLMNOPQRSTUVWXYZ0123456789";

    for (var i = 0; i < length; i++)
      text += possible.charAt(Math.floor(Math.random() * possible.length));
    return text;
  }

  newKey(){
    if (!confirm('Are you sure?')) return false
    this.server.psk_master = this.keyGen()
    return true
  }

  copyKey(e){
    // console.log(e)
    if (e.isSuccess) this.toastr.success('You have copied the key')
  }

  putKey(e){
    console.log(e.clipboardData)
  }

  makeClone(src){
    return JSON.parse(JSON.stringify(src));
  }

  save(){
    if ( !confirm('Are you sure? It will refresh all settings and create new instance of HA.') ) return
    this.loading.next(true)
    this.rootpw.next(false)
    this.output.next('')
    let data = this.makeClone(this.server)
    let inter = this.inter.split('-').map(Function.prototype.call, String.prototype.trim)
    data.slaves_ip = data.slaves_ip.split(',').map(Function.prototype.call, String.prototype.trim)
    data.interface = inter[0];
    data.ipaddr = inter[1];
    this.service.ha_post(data).subscribe( data => {
      this.initInfo()
      this.loading.next(false)
      console.log(data)
      this.validation.next(data.error.validation)
      if (data.error.status){
        this.loading.next(false)
        return
      }
      if ( data.response_e != ''){
        this.rootpw.next(!!data.rootpw)
        this.output.next(data.response_e)
        return
      }
      let message = ''
      if ( data.response && data.response.log){
        message = data.response.log.messages.join("\n")
        // for (let i = 0; i < data.response.log.messages.length; i++) {
        //     message += data.response.log.messages[i];
        // }
      }
      this.output.next(message)
      this.rootpw.next(data.rootpw)
    })
  }

  status(){
    this.output.next('')
    this.service.ha_status().subscribe( data => {
      console.log(data)
      this.output.next(data.status.log.messages.join("\n"))
    })
  }

  slaveRefresh(){
    if ( this.server.slaves_ip == '') {
      this.toastr.error('List can not be empty!')
      return
    }
    let list = this.makeClone(this.server.slaves_ip).split(',').map(Function.prototype.call, String.prototype.trim)
    this.service.ha_slave_list(list).subscribe( data => {
      console.log(data)
      this.validation.next(data.error.validation)
      if (data.error.status){
        this.loading.next(false)
        return
      }
      if (data.status) {
        this.toastr.success('List saved')
      }
    })
  }

}
