import { Component, OnInit } from '@angular/core';
import { BehaviorSubject } from 'rxjs';
//Toastr
import { ToastrService } from 'ngx-toastr';

//Service
import { SettingsService } from '../settings.service'

@Component({
  selector: 'kt-smtp',
  templateUrl: './smtp.component.html',
  styleUrls: ['./smtp.component.scss']
})
export class SmtpComponent implements OnInit {

  data = {
    smtp_auth: 1,
    smtp_autotls: 0,
    smtp_from: "",
    smtp_password: "",
    smtp_port: 25,
    smtp_secure: "",
    smtp_servers: "",
    smtp_username: "",
  }
  data_old = {
    smtp_password: ''
  }

  smtp_test_email=''
  send_status=''

  validation = new BehaviorSubject({})
  loading = new BehaviorSubject(true)

  constructor(
    private service: SettingsService,
    private toastr: ToastrService
  ) { }

  ngOnInit() {
    this.init()
  }

  makeClone(src){
    return JSON.parse(JSON.stringify(src));
  }

  init(){
    this.service.smtp_get().subscribe( data => {
      console.log(data)
      this.data = data.smtp
      this.data_old = this.makeClone(data.smtp)
      this.loading.next(false)
    })
  }

  save(){
    console.log(this.data)
    this.loading.next(true)
    if (JSON.stringify(this.data) == JSON.stringify(this.data_old)){
      this.toastr.warning('No Changes Detected!')
      this.loading.next(false)
      return;
    }

    let data = this.makeClone(this.data)
    data.smtp_auth += 0
    data.smtp_autotls += 0
    if (data.smtp_password == this.data_old.smtp_password) delete data.smtp_password

    console.log(data)

    this.service.smtp_post(data).subscribe( data => {
      console.log(data)
      this.validation.next(data.error.validation)
      if (data.error.status){
        this.loading.next(false)
        return
      }
      this.toastr.success('Changes Saved!')
      this.init()
      this.loading.next(false)
    })
  }

  send(){
    this.service.smtp_test(this.smtp_test_email).subscribe( data => {
      console.log(data)
      this.validation.next(data.error.validation)
      if (data.error.status){
        this.loading.next(false)
        return
      }
      this.send_status = data.result
    })
  }

}
